import { Field, InputType } from "@nestjs/graphql";
import { Type } from "class-transformer";
import { ArrayMinSize, IsArray, IsNotEmpty, IsString, ValidateNested } from "class-validator";
import { CreateBrandPaymentGatewayInput } from "./create-brand-payment-gateway-input";
import { CreateBrandPaymentGatewayPlanInput } from "./create-brand-payment-gateway-plan-input";


@InputType()
export class CreatePaymentGatewayKeyInput {

  @Field(() => CreateBrandPaymentGatewayInput)
  @ValidateNested()
  @Type(() => CreateBrandPaymentGatewayInput)
  paymentGateway: CreateBrandPaymentGatewayInput;

  @Field(() => String)
  @IsNotEmpty()
  @IsString()
  clientId: string;

  @Field(() => String)
  @IsNotEmpty()
  @IsString()
  clientSecret: string;

  @Field(() => [CreateBrandPaymentGatewayPlanInput])
  @ArrayMinSize(1)
  @ValidateNested({ each: true })
  @IsArray()
  @Type(() => CreateBrandPaymentGatewayPlanInput)
  paymentGatewayPlans: CreateBrandPaymentGatewayPlanInput[];

}
